<template>
  <div class="about">
    <!--<h1>This is an about page</h1>-->
    <div :key="index" v-for="(item, index) in list">
      <el-checkbox v-model="item.hidden">
        <span>展开/折叠</span>
      </el-checkbox>
      <el-checkbox @change="(e) => change(e, item)" v-model="item.selectAll">全选/取消全选</el-checkbox>
      <h2>{{ item.title }}</h2>
      <el-collapse-transition>
        <div class="tag_list" v-show="!item.hidden">
          <tag v-model="it.selected" v-for="it in item.heroList">
            {{ it.name }}
          </tag>
        </div>
      </el-collapse-transition>
    </div>
  </div>
</template>

<script setup>
import {reactive, ref, watch} from 'vue';
import Tag from '@/components/tag.vue';

const list = reactive(require('@/mock/tmp').data.list)
const checked2 = ref(false)
let selected = ref(false)
function change(val, item) {
  list.forEach(it => {
    it.heroList.forEach(h => {
      if (it.title === item.title) {
        h.selected = val
      }
    })
  })
}

watch(list, val => {
  val.forEach(item => {
    item.selectAll = item.heroList.every(it => it.selected)
  })
}, {
  deep: true
})
</script>
<style lang="less">
h2 {
  margin: 0;
}

.tag_list {
  display: flex;
  justify-content: center;
}
</style>
